export default {
	props: {
		show: {
			type: Boolean,
			default: false
		},
		type: {
			type: String,
			//top - 顶部， middle - 居中, bottom - 底部
			default: 'middle'
		},
		msg: {
			type: String,
			default: ""
		}
	},
	data() {
		let offsetTop = 0;



		return {
			offsetTop: offsetTop
		}
	},
	methods: {
		hide: function() {
			this.$emit('hidePopup');
		}
	}
}
